import React from "react";
import { Grid, Typography,Button } from "@mui/material";
import { makeStyles } from "@mui/styles";


const Gre = (Props) => {
  const classes = useStyles()
  return (
    /* university.jfif issue */
    <Grid
      sx={{
        width: "100%",
        margin:0,
        justifyContent: "space-around",
        backgroundImage: Props.slideImage,
        backgroundPosition:"center",
        backgroundSize:"cover",
        borderRadius: "15px",
      }}
    >

      <div className={classes.CardDesign}>

        <Typography sx={{ fontWeight:"700", fontSize:"14px", color:"#fff"}}>{Props.heading }</Typography>
        <Typography sx={{mt:1, color:"white" , fontSize:"12px", width:"230px"}}>{Props.text}</Typography>
        <Button variant="text" disableElevation sx={{ backgroundColor:"#fff",
              mt:2,
              padding:"3px 12px",
              fontWeight:"600",
            fontSize:"13px",
              color:"#1b6cfc",
              border:" 1px solid #DCE8FF",
              boxShadow: "0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04), 0px 0px 1px rgba(0, 0, 0, 0.04)",
              borderRadius: "30px"
               }}>More</Button>
        </div>
    </Grid>
  );
};
export default Gre;

const useStyles = makeStyles(() => ({
  CardDesign:{
    // background: linear-gradient("269.38deg, rgba(255, 255, 255, 0) 29.03%, rgba(43, 43, 43, 0.65) 69.97%"),
    background:"linear-gradient(269.38deg, rgba(255, 255, 255, 0) 29.03%, rgba(43, 43, 43, 0.65) 69.97%)",
    borderRadius:"15px",
    padding:"20px"
  }
}))